.bodyContainer {
  max-width: 1426px;
  margin: 0 auto; }

@media (min-width: 768px) {
  .container {
    padding: 24px; }
  .main__title::before {
    left: -1.5rem; } }

@media (min-width: 1336px) {
  .container {
    padding: 42px 34px; }
  .main__title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: baseline; }
  .main__title-wrapper.container {
    padding-bottom: 0;
    margin-bottom: 8px; }
  .main__title::before {
    left: -2.125rem; }
  .body__content {
    display: flex; }
  .body__content-wrapper {
    position: relative; } }

.burger-menu__footer-lang-item_active .burger-menu__footer-lang-button {
  color: var(--text-color-gray); }

.burger-menu__footer-button:active {
  opacity: 0.8; }

.burger-nav__item:active .burger-nav__item-button {
  color: rgba(27, 28, 33, 0.8); }

.burger-menu__footer-lang-item:active .burger-menu__footer-lang-button {
  color: rgba(27, 28, 33, 0.8); }

.swiper-filter::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 3.625rem;
  height: 100%;
  pointer-events: none;
  background: linear-gradient(to right, rgba(248, 248, 248, 0) 0%, #f8f8f8 100%);
  z-index: 1; }

@media (hover: hover) {
  .service__position-item:hover {
    outline: 0.125rem solid var(--background-active-color);
    border-radius: var(--border-radius-md);
    background-color: var(--background-white-color); }
  .service__position-item-link:hover {
    outline: 0.125rem solid var(--background-active-color);
    border-radius: var(--border-radius-md);
    background-color: var(--background-white-color); }
  .header-button:hover {
    opacity: 0.5; }
  .burger-menu__footer-button:hover {
    opacity: 0.5; }
  .burger-nav__item:hover .burger-nav__item-button {
    color: rgba(27, 28, 33, 0.5); }
  .burger-menu__footer-lang-item:hover .burger-menu__footer-lang-button {
    color: rgba(27, 28, 33, 0.5); } }
